let drawOnLoad = function(id) {
    let path = document.querySelector(`#${id}`);
    let pathLength = path.getTotalLength();
    path.style.strokeDasharray = pathLength + ' ' + pathLength;
    path.style.strokeDashoffset = pathLength;

    setTimeout(()=>{
        $(".main").addClass("_active")
    },1400)

    setTimeout(()=>{
        path.style.strokeDashoffset = 0
    },2000)
}

drawOnLoad("path-1")
drawOnLoad("path-2")


let drawFirst = function(id,nextBlock,divide) {
    let path = document.querySelector(`#${id}`);
    let pathLength = path.getTotalLength();
    path.style.strokeDasharray = pathLength + ' ' + pathLength;
    path.style.strokeDashoffset = pathLength;
    window.addEventListener("scroll", function(e) {
        let scrollPercentage = (document.documentElement.scrollTop + document.body.scrollTop) / (($(nextBlock).offset().top) - (window.innerHeight / divide))
        let drawLength = pathLength * scrollPercentage;
        path.style.strokeDashoffset = pathLength - drawLength;

    });
}

drawFirst("path-3",".stat",3)
drawFirst("path-4",".info-slider",3)
drawFirst("path-5",".info-slider",3)
drawFirst("path-6",".member",2)
drawFirst("path-7",".member",2)
drawFirst("path-8",".footer",2)
drawFirst("path-9",".footer",2)
