window.addEventListener('DOMContentLoaded', () => {
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        getScrollBarSize()
    })

    getScrollBarSize()

})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

$(".slideToggle-block__show").click(function () {
    $(this).parents(".slideToggle-block").toggleClass("slideToggle-block--active")
    $(this).siblings(".slideToggle-block__hidden").slideToggle()
})
